@import "reset";
@import "variables";
@import "breakpoint";

@import "colors";
@import "space";
@import "grid";
@import "fontSet";
@import "border";


@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,900&display=swap');

@font-face {
  font-family: 'Roboto';
  unicode-range: U+000-5FF, U+1e00-1fff, U+2000-2300;
}

body {
  font-family: 'Roboto','Noto Sans TC', sans-serif;
}


.header {
  padding: 1.875rem 5.5rem;
  color: $white;
  background-color: $main;
  @include pad {
    padding: 1.875rem 0;
  }
  @include mobile {
    padding-top: 0.625rem;
    padding-bottom: 0;
  }
}
.logo a{
  display: block;
  width: 5rem;
  height: 3.0625rem;
  background-image: url(../images/logo.png);
  background-repeat: no-repeat;
  background-size: cover;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden;
  @include mobile{
    width: 3rem;
    height: 1.8125rem;
    margin-bottom: 0.625rem;
  }
}

.footer {
  padding-top: 2rem;
  padding-bottom: 2rem;
  color: $white;
  background-color: $main;
}

.link {
  text-decoration: none;
  &--underline{
    position: relative;
    &::after {
    position: absolute;
    width: 100%;
    top: 100;
    display:block;
    content: '';
    border-bottom: solid 2px $white;
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
    }
    &:hover::after,:focus::after{
        transform: scaleX(1);
    }
  }
  &--block::after{
    content: '';
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.active {
  border-bottom: .5rem solid $main;
}

.icon {
  width: 3rem;
  height: 3rem;
  @include pad {
    width: 1.5rem;
    height: 1.5rem;
    max-width: min-content;
  }
  @media (max-width: 320px) {
    display:none;
  }
}

.icon-list {
  max-width: 2.75rem;
  max-height: 2.75rem;
  width: 100%;
  height: 100%;
  @include pad {
    max-width: 2rem;
    max-height: 2rem;
  }
  @include mobile {
    max-width: 1rem;
    max-height: 1rem;
  }
}

.img {
  width: calc(100vw);
  object-fit: cover;
}

.hide {
  display: none;
}
.hide-md {
  @include pad {
    display: none;
  }
}
.hide-sm {
  @include mobile {
    display: none;
  }
}
.text-center {
  text-align: center;
}

.product {
  flex-grow: 1;
  flex-basis: 0;
}

.product-img {
  height: 10.25rem;
}

.product-color {
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: .25rem;
  margin-right: .5rem;
  margin-bottom: 6.5rem;
  &--dark {
    background-color: #5F3E2D;
  }
  &--light {
    background-color: #B75929;
  }
  @include pad{
    margin-bottom: 4.5rem;
  }
  @include mobile{
    margin-bottom: 2rem;
  }
}

.pagination {
  font-size: 1.125rem;
  padding: .75rem 1rem;
  &--page {
    padding: .6875rem .75rem;
  }
}